import { places } from './data';
import { useTranslation } from 'next-i18next';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination } from 'swiper/modules';
import Link from 'next/link';
import Image from 'next/image';

import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';

export default function Home() {
  const { t } = useTranslation();

  return (
    <div className="min-h-screen bg-white p-6">
      <h1 className="text-3xl font-bold mb-8">{t('popularDestinations')}</h1>
      
      <Swiper
        modules={[Navigation, Pagination]}
        spaceBetween={30}
        slidesPerView={1}
        navigation
        pagination={{ clickable: true }}
        breakpoints={{
          640: { slidesPerView: 2 },
          1024: { slidesPerView: 3 }
        }}
        className="mySwiper"
      >
        {places.map((place) => (
          <SwiperSlide key={place.name}>
            <Link href={`/${place.name.toLowerCase().replace(/\s+/g, '-')}`}>
              <div className="rounded-lg overflow-hidden shadow-lg border border-gray-200 h-full flex flex-col">
                <div className="relative h-64">
                  <Image
                    src={place.image}
                    alt={place.name}
                    fill
                    className="object-cover"
                  />
                </div>
                <div className="p-4 flex-grow">
                  <h2 className="text-xl font-bold">{place.name}</h2>
                  <p className="text-gray-600 mt-2">{place.description}</p>
                </div>
                <div className="p-4 bg-gray-50">
                  <p>{place.travelTime}</p>
                  <p>{place.price}</p>
                </div>
              </div>
            </Link>
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
}

export { places };